.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  padding-top: 48px;
}

.title-container {
  display: flex;
  margin-bottom: 36px;
  justify-content: space-between;
  width: calc(100% - 48px);
  margin-left: 24px;
  margin-right: 24px;
}

.page-title {
  text-align: center;
}

.clear-game,
.spacer {
  width: 100px;
}

.solver-body {
  display: flex;
  flex-direction: column;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
